.jumbotron {
    height: 400px;
    background-image: url(/static/media/game.5a56443e.jpeg);
    height: calc(70vh - 66px);
    padding-top: 120px;
    text-transform: uppercase;
    text-align: center;
}

.lead, h1 {
    text-transform: uppercase;
    color: white;
}
.fixed-top {
    height: 60px;
    background-color: #bfbfbf;
    font-size: 40px;
    color: white;
    justify-content: space-around;
    width: 100%;
}


p {
    text-transform: uppercase;
    color: white;
}



.fixed-bottom {
    height: 60px;
    background-color: #bfbfbf;
    font-size: 20px;
    color: white;
    justify-content: space-around;
    width: 100%;
    margin-bottom: 0px;
}


p {
    text-transform: uppercase;
    color: white;
    text-align: center;
}
.card {
  padding: 10px;
  margin-bottom: 10px;
  border: none;
  text-align: center;
  }
  
   .card img {
    margin: auto;
    padding: 20px;
    border: 1px solid rgba(0, 0, 0, 0.25);
    max-height: 200px;
    max-width: 200px;
    background-color: #bfbfbf;
  }
  
  /* .card > .content > ul {
    list-style-type: none;
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
  } */
.gameContainer {
    margin-bottom: 50px;
    padding: 0; 
}

.gameMessage {
    text-align: center;
    max-width: 100%;
    align-content: center
}

.gameMessage p {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    background-color: #777777;
    color: white;
    padding: 20px 0;
    margin-bottom: 0px;
}

.gameScores {
    text-align: center;
    max-width: 100%;
    align-content: center;
    margin-bottom: 10px;
}
.gameScores p {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    background-color:#e7283a;
    color: white;
    padding: 20px 0;
    margin-bottom: 25px;
}
.mainContainer {
  padding: 0; 
  background-color: rgba(0, 0, 0, 0.03);
  min-height: 100vh;
  padding-bottom: 100px;
  }
